@import url(https://fonts.googleapis.com/css2?family=Asap&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Asap', sans-serif;
  background-color: #020211;
  color: white;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 72px;
  color: white;
}

.coin.search {
  margin-bottom: 72px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.coin-text {
  margin-bottom: 39px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 500px;
  height: 40px;
  border-radius: 2.5px;
  border: none;
  background-color: #f7f7ff;
  color: #cc444b;
  font-weight: 700;
  margin-bottom: 20px;
}

.coin-input::-webkit-input-placeholder {
  color: #000814;
}

.coin-input::placeholder {
  color: #000814;
}
.coin-container {
    display: flex;
    justify-content: center;
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
  
    min-width: 300px;
  }
  
  .coin h1 {
    font-size: 16px;
    width: 150px;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
  }
  
  .coin-data {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
  }
  
  .coin-price {
    width: 110px;
  }
  
  .coin-volume {
    width: 155px;
  }
  
  .coin-marketcap {
    width: 230px;
  }
  
  .coin-percent {
    width: 100px;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
